/**
 * COMMENT.
 */
import React from "react"
//import { Link as GatsbyLink } from "gatsby"


/**
 * Page Meta.
 */
const pageMeta = {
  title: 'Comment',
  description: 'Contenu de la page Comment...',
}

/**
 * React component (JSX).
 */
const CommentPage = () => (
  <>
  <div className="content">
    <h1>{pageMeta.title}</h1>
    <p>Bienvenue dans la page Comment...</p>
  </div>
  </>
)

CommentPage.pageMeta = pageMeta 

export default CommentPage
